.fg {
  position: relative;
  margin: 0;
  input,
  textarea{
    border: 1px solid #D9D9D9;
    border-radius: 8px;
    padding: 0 20px 0 44px;
    height: 60px;
    width: 100%;
    font-weight: 500;
    font-size: 14px;
    background-color: transparent;
    color: #000;
    &::placeholder {
      color: #7A879C;
    }
  }
  svg{
    position: absolute;
    top: 50%;
    left: 12px;
    transform: translateY(-50%);
  }
  textarea{
    padding: 8px 20px;
    height: 138px;
  }
  .invalid-input{
    border-color: #F93232;
  }
}

.check {
  position: relative;
  input {
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
    &:checked+label {
      &::before {
        // background-color: $green;
        border: 3px solid #fff;
      }
    }
    &:disabled+label {
      color: #AAAAAA;
      pointer-events: none;
    }
  }
  label {
    padding-left: 40px;
    position: relative;
    min-height: 20px;
    font-size: 14px;
    line-height: 20px;
    cursor: pointer;
    &:before {
      content: '';
      position: absolute;
      z-index: 1;
      border-radius: 2px;
      background: rgba(255, 255, 255, 0.01);
      border: 1px solid rgba(255, 255, 255, 0.45);
      width: 20px;
      height: 20px;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
      transition: all .100s;
    }
  }
}
