.header{
  background: #FFFFFF;
  border-bottom: 1px solid #D9D9D9;
  padding: 18px 0;
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__logo{
    display: block;
    width: 253px;
    img{
      width: 100%;
      display: block;
    }
  }
}

.burger{
  display: none;
}

.nav{
  display: flex;
  align-items: center;
  &__link{
    font-size: 14px;
    line-height: 130%;
    &:not(:last-child){
      margin-right: 60px;
    }
  }
  &__bg{
    display: none;
  }
}


@media screen and (max-width: 992px){
  .header{
    padding: 15px 0;
    &__logo{
      width: 208px;
    }
    &._open{
      .burger{
        span{
          &:nth-child(1){
            transform: rotate(-45deg) translate(-5.5px,5px);
          }
          &:nth-child(2){
            transform: scale(0);
          }
          &:nth-child(3){
            transform: rotate(45deg) translate(-5.5px,-5px);
          }
        }
      }
      .nav{
        opacity: 1;
        pointer-events: unset;
        &__bg{
          display: block;
        }
      }
    }
  }

  .burger{
    width: 23px;
    height: 17px;
    display: flex;
    align-items: center;
    position: relative;
    z-index: 25;
    span{
      display: block;
      width: 100%;
      height: 2px;
      background: $black;
      left: 0;
      transition: all .3s;
      &:nth-child(1){
        position: absolute;
        top: 0;
      }
      &:nth-child(3){
        position: absolute;
        bottom: 0;
      }
    }
  }

  .nav{
    position: fixed;
    padding: 92px 20px;
    height: 100vh;
    top: 0;
    right: 0;
    opacity: 0;
    z-index: 15;
    background-color: #fff;
    flex-direction: column;
    transition: all .3s;
    pointer-events: none;
    &__bg{
      height: 100vh;
      width: 100%;
      position: fixed;
      top: 0;
      left: 0;
      z-index: 14;
      background: rgba(0, 0, 0, 0.5);
    }
    &__link{
      font-size: 20px;
      line-height: 140%;
      margin-left: auto;
      &:not(:last-child){
        margin-right: 0;
        margin-bottom: 20px;
      }
    }
  }
}
