.what-card{
  padding: 37px 40px 32px;
  text-align: center;
  &__icon{
    width: 90px;
    display: block;
    margin: 0 auto;
    img{
      width: 100%;
    }
  }
  &__title{
    margin: 24px 0 16px;
    font-weight: 500;
    font-size: 20px;
    line-height: 130%;
  }
}


@media screen and (max-width: 992px){
  .what-card{
    padding: 20px;
  }
}


@media screen and (max-width: 768px){
  .what-card{
    padding: 0;
  }
}
