.main{
  padding: 120px 0 42px;
  &__inner{
    display: grid;
    align-items: center;
    grid-template-columns: auto 31.25%;
    gap: 4%;
  }
  &__img{
    width: 100%;
    display: block;
  }
  &__title{
    background: linear-gradient(276.27deg, #9AA6F6 13.5%, #FF8BE5 96.46%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-fill-color: transparent;
    font-weight: 700;
    font-size: 64px;
    line-height: 127.4%;
    letter-spacing: -0.03em;
    text-transform: uppercase;
  }
}

.who{
  padding: 42px 0 60px;
  &__inner{
    display: grid;
    grid-template-columns: 338px auto;
    gap: 20px;
  }
  &__title{
    padding-top: 10px;
  }
}

.line{
  position: relative;
  padding-bottom: 20px;
  &:before{
    content: '';
    position: absolute;
    width: 60px;
    height: 4px;
    left: 0;
    bottom: 0;
    border-radius: 6px;
    background-color: $primary;
  }
}

.what{
  padding: 60px 0;
  &__title{
    text-align: center;
  }
  &__desc{
    text-align: center;
    margin-top: 20px;
    &:before{
      left: 50%;
      transform: translateX(-50%);
    }
  }
  &__grid{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 30px;
    margin-top: 40px;
  }
}

.contact{
  padding: 60px 0;
  &__title{
    text-align: center;
    &:before{
      left: 50%;
      transform: translateX(-50%);
    }
  }
  &__form{
    margin-top: 40px;
  }
  &__grid{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 20px 30px;
  }
  &__full{
    grid-column: 1/-1;
  }
  &__btn{
    font-weight: 500;
    font-size: 18px;
    line-height: 21px;
    color: #FFFFFF;
    background-color: $primary;
    width: 195px;
    height: 61px;
    margin: 44px auto 0;
    border-radius: 8px;
    display: flex;
    align-items: center;
    justify-content: center;
    &:hover{
      background-color: darken($primary,10%);
    }
  }
}


@media screen and (max-width: 1200px){
  .what{
    &__grid{
      grid-template-columns: 1fr 1fr;
    }
  }
}


@media screen and (max-width: 992px){
  .main{
    padding: 14px 0;
    &__inner{
      grid-template-columns: 100%;
    }
    &__title{
      text-align: center;
      font-size: 32px;
    }
    &__img{
      display: none;
    }
  }

  .who{
    padding: 14px 0 20px;
    text-align: center;
    &__inner{
      grid-template-columns: 100%;
    }
    &__title{
      padding-top: 0;
      &:before{
        left: 50%;
        transform: translateX(-50%);
      }
    }
  }

  .what{
    padding: 20px 0;
    &__grid{
      gap: 16px;
      margin-top: 20px;
    }
  }

  .contact{
    padding: 24px 0;
    &__form{
      margin-top: 20px;
    }
    &__grid{
      grid-template-columns: 1fr;
      gap: 20px;
    }
  }
}


@media screen and (max-width: 768px){
  .what{
    &__grid{
      grid-template-columns: 100%;
    }
  }
}
