.footer{
  padding: 20px 0 28px;
  background: #F5F7F9;
  margin-top: 60px;
  &__inner{
    display: flex;
    justify-content: space-between;
  }
  &__left{
    display: flex;
  }
  &__by,
  &__c,
  &__address,
  &__mail{
    font-weight: 500;
    font-size: 12px;
    line-height: 130%;
  }
  &__c{
    color: $secondary;
    margin-left: 20px;
  }
  &__address{
    color: $secondary;
    margin-bottom: 8px;
  }
  &__right{
    text-align: right;
  }
}


@media screen and (max-width: 992px){
  .footer{
    padding: 20px 0 40px;
    &__inner{
      display: block;
    }
    &__left{
      justify-content: center;
    }
    &__right{
      text-align: center;
    }
    &__address{
      margin-top: 28px;
      width: 100%;
    }
  }
}
