.display2{
  font-weight: 400;
  font-size: 30px;
  line-height: 130%;
}

.body-text,
.body-text2{
  line-height: 140%;
  p{
    line-height: inherit;
    &:not(:last-child){
      margin-bottom: 10px;
    }
  }
}

.body-text{
  font-size: 20px;
}

.body-text2{
  font-size: 18px;
}

.mlist{
  li{
    line-height: inherit;
    display: flex;
    &::before{
      content: '';
      display: block;
      width: 4px;
      height: 4px;
      border-radius: 50%;
      background-color: #000;
      transform: translateY(8px);
      margin-right: 5px;
      flex-shrink: 0;
    }
    &:not(:last-child){
      margin-bottom: 16px;
    }
  }
}
